import axios from 'axios'
import { Toast } from 'antd-mobile'

// ---loading
axios.interceptors.request.use(function (config) {
  Toast.loading('', 0)
  return config
})

// 
axios.interceptors.request.use(function (config) {
  Toast.hide()
  setTimeout(() => {
    Toast.hide()
  }, 500)
  return config
})


